.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

.label {
    font-size: 18px;
    margin-bottom: 10px;
}

.input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 20px;
}

.select {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 20px;
}

.options {
    font-size: 16px;
}

.user-list {
    margin-top: 20px;
}

.user-card {
    background-color: #f9f9f9;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
}

.user-name {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
}

.username {
    font-size: 16px;
    color: #666;
}

.link {
    display: block;
    margin-top: 10px;
    text-decoration: none;
    color: #007bff;
}

.link:hover {
    text-decoration: underline;
    color: #0056b3;
}